import { React, useEffect, useState } from "react";
import SortPopup from "../components/sortPopup/sortPopup";

import { useSelector } from "react-redux";

import axios from "axios";
import ProductBlock from "../components/product-block/product-block";

import LoadingSpinner from "../components/loadingSpinner/LoadingSpinner";
import SearchBlock from "../components/search-block/search-block";

function Home() {
   const sortItem = useSelector((state) => state.filter.sortItem);

   let searchValue = useSelector((state) => state.filter.searchValue);

   let sortNames = ["rating", "price", "title"];

   let [data, setData] = useState([]);

   let [isLoading, setIsLoading] = useState(true);

   let sorting = (sortItem) => {
      if (sortItem === 0) {
         return "rating";
      }
      if (sortItem === 1) {
         return "price";
      }
      if (sortItem === 2) {
         return "title";
      }
   };

   useEffect(() => {
      let search = searchValue ? `&search=${searchValue.toLowerCase()}` : "";

      setIsLoading(true);
      axios
         .get(
            `https://6471d1bd6a9370d5a41ab75b.mockapi.io/carts?${`sortBy=${sorting(
               sortItem
            )}${search}&order=desc`}`
         )
         .then((items) => {
            setData(items.data);
            setIsLoading(false);
         });
   }, [sortItem, searchValue]);

   return (
      <div className="content">
         <div className="container">
            <div className="content__top"></div>
            <h2 className="content__title">Our products:</h2>
            <div className="content__filters-row">
               <SortPopup items={sortNames} className="sort-popup" />
               <SearchBlock className="header__search" />
            </div>
            {isLoading ? (
               <LoadingSpinner />
            ) : (
               <div className="content__items">
                  {isLoading
                     ? null
                     : data.length ? data.map((obj, i) => {
                          return <ProductBlock key={obj + "_" + i} {...obj} />;
                       }) : <p style={{textAlign: "center", fontWeight: "700", fontSize: "20px", margin: "50px auto"}}>We don't have this product :(</p>}
               </div>
            )}
         </div>
      </div>
   );
}

export default Home;
