import React, { useState, useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setSort } from "../../redux/slices/filterSlice";

const SortPopup = ({ items }) => {
   let [sortPopupState, setSortPopupState] = useState(false);
   const dispatch = useDispatch();
   const sortItem = useSelector((state) => state.filter.sortItem);

   const openPopup = () => {
      setSortPopupState(!sortPopupState);
   };

   const sortCurrent = (sortItem) => {
      dispatch(setSort(sortItem));
   };

   const currentRef = useRef();

   useEffect(() => {
      const clickOutside = (e) => {
         console.log();
         if (!e.target.parentNode.classList.contains("sort__label")) {
            setSortPopupState(false);
            console.log("out");
         }
      };

      document.body.addEventListener("click", clickOutside);

      return () => document.body.removeEventListener("click", clickOutside);
   }, []);

   return (
      <div className="sort" ref={currentRef}>
         <div className="sort__label">
            <svg
               fill="currentColor"
               class="bi bi-arrow-down-short"
               version="1.1"
               id="Layer_1"
               width="30"
               height="60"
               viewBox="0 0 20 14"
               xmlns="http://www.w3.org/2000/svg"
               className={sortPopupState ? "rotated" : ""}
            >
               {" "}
               <path
                  fill-rule="evenodd"
                  d="M8 4a.5.5 0 0 1 .5.5v5.793l2.146-2.147a.5.5 0 0 1 .708.708l-3 3a.5.5 0 0 1-.708 0l-3-3a.5.5 0 1 1 .708-.708L7.5 10.293V4.5A.5.5 0 0 1 8 4z"
               />{" "}
            </svg>
            <b>Sort by:</b>
            <span onClick={openPopup}>{items[sortItem]}</span>
         </div>
         {sortPopupState && (
            <div className="sort__popup">
               <ul>
                  {items.map((name, index) => {
                     return (
                        <li
                           className={sortItem === index ? "active" : ""}
                           onClick={() => {
                              sortCurrent(index);
                              openPopup();
                           }}
                           key={name + "__" + index}
                        >
                           {name}
                        </li>
                     );
                  })}
               </ul>
            </div>
         )}
      </div>
   );
};

export default SortPopup;
