import React from "react";

import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import CartItem from "../components/cart-item/cartItem";
import { clearItems } from "../redux/slices/cartSlice";
import CartEmpty from "./cart-empty";

function сart() {
   const dispatch = useDispatch();
   let { totalPrice, items } = useSelector((state) => state.cart);

   const totalCount = items.reduce((sum, item) => sum + item.count, 0);

   const onClickClear = () => {
      dispatch(clearItems());
   };
   return (
      <div class="content">
         <div class="container container--cart">
            {items.length ? (
               <>
                  <div class="cart">
                     <div class="cart__top">
                        <h2 class="content__title">
                           Your cart have:
                        </h2>
                        <div class="cart__clear">
                           <span style={{fontWeight: "700", color: "#555", fontSize: "20px"}} onClick={onClickClear}>Clean ther cart</span>
                        </div>
                     </div>
                     <div class="content__items">
                        {items.map((item) => {
                           return <CartItem key={item.id} {...item} />;
                        })}
                     </div>
                     <div class="cart__bottom">
                        <div class="cart__bottom-details">
                           <span>
                              {" "}
                              Count of products: <b>{totalCount} pice.</b>{" "}
                           </span>
                           <span>
                              {" "}
                              Total price: <b>{totalPrice} KZT</b>{" "}
                           </span>
                        </div>
                        <div class="cart__bottom-buttons">
                           <Link to="/">
                              <div class="button button--add go-back-btn">
                                 <svg
                                    width="8"
                                    height="14"
                                    viewBox="0 0 8 14"
                                    fill="none"
                                    xmlns="http://www.w3.org/2000/svg"
                                 >
                                    <path
                                       d="M7 13L1 6.93015L6.86175 1"
                                       stroke="#D3D3D3"
                                       stroke-width="1.5"
                                       stroke-linecap="round"
                                       stroke-linejoin="round"
                                    />
                                 </svg>

                                 <span>Go back</span>
                              </div>
                           </Link>
                           <div class="button pay-btn">
                              <span onClick={()=> alert('Pay is successful!')}>Go to pay</span>
                           </div>
                        </div>
                     </div>
                  </div>
               </>
            ) : (
               <CartEmpty />
            )}
         </div>
      </div>
   );
}

export default сart;
