import {
	createSlice
} from "@reduxjs/toolkit"

const initialState = {
	activeCategory: 0,
	sortItem: 0,
	searchValue: "",
}

export const filterSlice = createSlice({
	name: 'filter',
	initialState,
	reducers: {
		setActiveCategory: (state, actions) => {
			state.activeCategory = actions.payload;
		},
		setSort: (state, actions) => {
			state.sortItem = actions.payload
		},
		setSearchValue: (state, actions) => {
			state.searchValue = actions.payload;
		}
	},
})

export const {
	setActiveCategory,
	setSort,
	setSearchValue
} = filterSlice.actions

export default filterSlice.reducer