import { useRef } from "react";
import { React, useCallback } from "react";

import { useDispatch } from "react-redux";
import { setSearchValue } from "../../redux/slices/filterSlice";
import debounce from "lodash.debounce";
import { useState } from "react";

function SearchBlock({ className }) {
   let [value, setValue] = useState("");
   const inputRef = useRef();

   const onClearInput = () => {
      setValue("");
      inputRef.current.focus();
   };

   const dispatch = useDispatch();

   const ubdateSearchValue = useCallback(
      debounce((value) => dispatch(setSearchValue(value)), 1500),
      [value]
   );

   const onChengeInput = (e) => {
      ubdateSearchValue(e.target.value);
      setValue(e.target.value);
   };
   // let searcValue = inputRef.current.value

   return (
      <div className={"search-block " + className}>
         <input
            onChange={onChengeInput}
            ref={inputRef}
            value={value}
            className="search-block__input"
            type="text"
            placeholder="Search..."
         />
         {value && (
            <svg
               onClick={() => {
                  onClearInput();
               }}
               className="search-block__icon"
               xmlns="http://www.w3.org/2000/svg"
               viewBox="0 0 50 50"
               width="50px"
               height="50px"
            >
               <path
                  fill="none"
                  strokeMiterlimit="10"
                  strokeWidth="4"
                  d="M7.741 7.741L42.359 42.359M42.258 7.742L7.618 42.382"
               />
            </svg>
         )}
      </div>
   );
}

export default SearchBlock;
