import React from "react";

function CartEmpty(props) {
   return (
      <div class="content">
         <div class="container container--cart">
            <div class="cart cart--empty">
               <h2>
                  Cart is empty!
               </h2>
               <img />
               <a href="/" class="button button--black">
                  <span>Go back</span>
               </a>
            </div>
         </div>
      </div>
   );
}

export default CartEmpty;
